    color: #cccccc;
    font-family: 'Varela Round', sans-serif;
}

body {
    color: #cccccc;
    font-family: 'Questrial', sans-serif;
} 

h1,h2 {
    color: #ED7207;
}
.Presentation {
    text-align: center;
    margin: auto;
    height: 110vh;
    background-color: #191919;
    position: relative;
    z-index: -1;
}

.Presentation__content {
    position: fixed;
    width: 100%;
}

.foto {
    margin-top:20vh;
    width: 20%;
    max-width: 445px;
    min-width: 200px;
    border-radius: 50%;
    border-style: solid;
    border-width: 2em;
    border-color: #353535;
    -webkit-animation-name: grow;
            animation-name: grow;
    -webkit-animation-duration: 0.6s;
            animation-duration: 0.6s;
}

.resumo{
    line-height: 1.5;
    margin: 0;
    padding-bottom: 2%;
    -webkit-animation-name: up;
            animation-name: up;
    -webkit-animation-duration: 0.6s;
            animation-duration: 0.6s;
}

.nome {
    width: inherit;
    margin: auto;
    padding-top: 2em;
    max-width: 360px;
    min-width: 200px;
    -webkit-animation-name: up;
            animation-name: up;
    -webkit-animation-duration: 0.6s;
            animation-duration: 0.6s;
}

.seta {
    width: 5%;
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
}

.wrapper {
    margin: auto;
    width: 3em;
    height: 3em;
    border-radius: 50%;
    border: 1px solid #cccccc;
    -webkit-animation: animate 2s linear infinite;
            animation: animate 2s linear infinite;
}

.seta-pulse {
    width: 2em;
    margin-top: 40%;
}

@-webkit-keyframes grow {
    0% {
        width: 0%; 
        min-width: 0;
        margin-top: 40vh;
        -webkit-transform: rotate(270deg);
                transform: rotate(270deg);
    }
    100% {
        width: 20%;
        min-width: 200px;
        margin-top: 20vh;
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes grow {
    0% {
        width: 0%; 
        min-width: 0;
        margin-top: 40vh;
        -webkit-transform: rotate(270deg);
                transform: rotate(270deg);
    }
    100% {
        width: 20%;
        min-width: 200px;
        margin-top: 20vh;
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@-webkit-keyframes up {
    0% {
        margin-top: 50vh;
    }
    30% {
        margin-top: 50vh;
    }
    100% {
        margin-top: 0vh;
    }
}

@keyframes up {
    0% {
        margin-top: 50vh;
    }
    30% {
        margin-top: 50vh;
    }
    100% {
        margin-top: 0vh;
    }
}

@-webkit-keyframes animate {
    0% {
        box-shadow: 0 0 0 0 rgba(204, 204, 204, .7), 0 0 0 0 rgba(204, 204, 204, .7);
    }
    40% {
        box-shadow: 0 0 0 10px rgba(204, 204, 204, 0), 0 0 0 0 rgba(204, 204, 204, .7);
    }
    80% {
        box-shadow: 0 0 0 10px rgba(204, 204, 204, 0), 0 0 0 0 rgba(204, 204, 204, 0);
    }
    100% {
        box-shadow: 0 0 0 0 rgba(204, 204, 204, 0), 0 0 0 0 rgba(204, 204, 204, 0);
    }
}

@keyframes animate {
    0% {
        box-shadow: 0 0 0 0 rgba(204, 204, 204, .7), 0 0 0 0 rgba(204, 204, 204, .7);
    }
    40% {
        box-shadow: 0 0 0 10px rgba(204, 204, 204, 0), 0 0 0 0 rgba(204, 204, 204, .7);
    }
    80% {
        box-shadow: 0 0 0 10px rgba(204, 204, 204, 0), 0 0 0 0 rgba(204, 204, 204, 0);
    }
    100% {
        box-shadow: 0 0 0 0 rgba(204, 204, 204, 0), 0 0 0 0 rgba(204, 204, 204, 0);
    }
}

.area {
    padding-bottom: 2%;
}
.Tag {
    width: 18em;
    height: 3em;
    text-align: center;
    border-radius: 0.2em;
    margin: auto;
    margin-top: 1em;
    margin-left: 0.5em;
    margin-right: 0.5em;
    border-left: #ED7207 solid;
    display: block;

    background: -webkit-linear-gradient(left, #ED7207 50%, #191919 50%);

    background: linear-gradient(to right, #ED7207 50%, #191919 50%);
    background-size: 200% 100%;
    background-position: right bottom;
    -webkit-transition: all .5s ease-out;
    transition: all .5s ease-out;
}

.Tag:hover {
    background-position: left bottom;
}

.TagName {
    margin: 1em;
    color: #cccccc;
}

@media (max-width:320px){
    .Tag {
        width: 10em;
    }
}
